
import axios from 'axios'
import XHeader from 'vux/src/components/x-header/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
components: { XHeader, GroupTitle, Flexbox, FlexboxItem, XButton },
data() {
return {

partyBranch:'',
name1:'',
departmentname:'',
totalscore:'',
pointdetail:''


}
},
filters: {
Upper: function (value) {
return value> 0 ? '+' + value : value;
}
},

methods: {

getParams() {

let item= this.$route.params.item;
this.name1=item.name;
this.departmentname=item.departmentname;
this.totalscore=item.totalscore;
axios({
method: 'get',
url: 'pdepartment/queryById',
params: {
departmentid:item.departmentid

}
}) .then((res)=> {
this.partyBranch=res.data.partyBranch;


console.log(res.data)
}).catch(function (error) {
console.log(error);
})
},
getPoint() {
let item= this.$route.params.item;
axios({
method: 'get',
url: 'pscoreparty/queryByUserId',
params: {
userid:item.id,
year:2018
}
}) .then((res)=> {
this.pointdetail=res.data


console.log("1212121212",res.data)
}).catch(function (error) {
console.log(error);
})




}
},mounted() {
this.getParams();
this.getPoint()
}


};



